.landing-promo-container {
  margin-bottom: 50px;
  @media screen and (min-width: 991px) {
    margin-bottom: 20px;
  }

  .header-padding {
    padding: 0 15px;

    @media screen and (max-width: 991px) {
      max-width: 600px;
      margin: auto;
    }
  }

  .promo-img {
    max-width: 100%;
    max-height: 150px;
    // width: 208px;
    padding: 10px;

    // @media screen and (max-width: 991px) {
    //   max-width: 100%;
    //   width: 200px;
    //   height: 200px / 244px * 168px;
      
    // }

    margin: auto;
    object-fit: contain;
    border-radius: 20px;

    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);
    margin-bottom: 30px;

    transition: transform 0.3s ease;

    &:hover {
      transform: scale(1.1);
    }
  }
}
